/* ======================== */

/* product page */

.freedelivery {
  /* delivery icon on product page */
  border: 2px solid green;
}

.portfolio-small-image {
  border: 1px solid lightgray;
}
.product-small-images {
  max-height: 500px;
  overflow-y: auto;
  cursor: pointer;
}
.product-small-images::-webkit-scrollbar {
  display: none;
}
/* ================================ */
/* range slider */
.rc-slider-handle-dragging.rc-slider-handle-dragging.rc-slider-handle-dragging {
  border-color: rgb(189, 50, 40) !important;
  box-shadow: 0 0 0 5px rgb(189, 50, 40) !important;
}
.rc-slider-rail {
  background-color: black !important;
}
.rc-slider-track {
  background-color: red !important;
}
.rc-slider-handle {
  opacity: 1 !important;
  border: solid 2px rgb(189, 50, 40) !important;
}

/* header */
.fl-header {
  transition: 0.3s;
}
.fl-header.is-sticky {
  position: sticky;
  top: 0;
  left: 0;
  z-index: 99;
  background: white;
  box-shadow: rgb(47, 38, 77) 0px 20px 30px -10px;
}
.loginbtn a {
  /* padding: 0; */
  color: white;
  font-size: 15px;
}
.loginbtn {
  background-color: rgb(189, 50, 40);
  border: none;
  padding: 8px 30px;
}

/* cart page */
.coupon-animation-container {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  /* transform: translate(-50%, -50%); */
  z-index: 9999;
  background-color: rgba(0, 0, 0, 0.356);
}

.coupon-animation {
  padding: 20px;
  /* border-radius: 20px; */
  color: white;
}
.coupon-animation h3 {
  color: white;
}

.plantmore-product-quantity b {
  padding: 5px 20px;
}
.plantmore-product-quantity button {
  padding: 0px 5px;
}
.plantmore-product-thumbnail img {
  max-height: 100%;
  object-fit: contain;
  max-width: 100%;
  height: 100px;
}
/* checkout */
.appliedcoupon {
  border: 2px dotted green;
  background-color: rgba(182, 226, 182, 0.425);
  padding: 5px 5px;
}
.appliedcoupon i {
  cursor: pointer;
}
.cart-items {
  min-height: 180px;
  max-height: 400px;
  overflow-y: auto;
}

/* For Webkit browsers (Chrome, Safari) */
::-webkit-scrollbar {
  width: 8px; /* Width of the scrollbar */
}

::-webkit-scrollbar-track {
  background: #f1f1f1; /* Background color of the track */
}

::-webkit-scrollbar-thumb {
  background: rgb(189, 50, 40); /* Color of the scrollbar thumb */
}

::-webkit-scrollbar-thumb:hover {
  background: #1e0000; /* Color of the scrollbar thumb on hover */
}
.products-list {
  position: fixed;
  top: 83px;
  left: 5vw;
  right: 5vw;
  background-color: white;
  max-width: 90vw;
  max-height: 430px;
  /* overflow-y: auto; */
  /* border: 10px solid rgba(0, 0, 0, 0); */
  box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.5);
  transition: 0.5s ease-in-out;
  z-index: 999;
}
.products-list-left img {
  padding: 15px;
  max-height: 100%;
  width: 100%;
}
.products-list-row{
  overflow-y: auto;
  max-height: 350px;
}
.cat-items > a {
  padding: 10px 0 !important;
  height: 70px !important;
}
.cat-items > a > img {
  object-fit: contain;
  height: 100%;
}

.fa-sort-down {
  transition: transform 0.3s ease;
}

.rotate {
  transform: rotate(180deg);
}

.cart-table tbody {
  max-height: 500px;
  overflow-y: auto;
}

