}
.allcat .single-banner {
  overflow: hidden;
  height: 266px;
  width: 266px;
}
.home.single-banner {
  overflow: hidden;
  height: 352px;
  width: 352px;
}
.single-banner img {
  max-height: 100%;
  max-width: 100%;
}
.banner-head {
  position: absolute;
  top: 0%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px;
  width: 100%;
  /* height: 100%; */
  transition: top 0.3s ease-in-out;
  background: rgba(0, 0, 0, 0.749);
}
.banner-head-text h4 {
  text-align: center;
  color: white;
  padding: 5px;
}
.single-banner:hover .banner-head {
  overflow: hidden;
  top: -100%;
  transition: top 0.3s ease-in-out;
}

/* product slider */
.product.carousel.slide {
  background-image: url(../public/assets/images/bg/productslider.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position-x: center;
  height: 700px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.product.carousel.slide .banner.carousel-inner {
  width: 315px;
  height: 600px;
  border-radius: 30px;
  /* z-index: ; */
}
.product.carousel.slide .banner.carousel-inner .banner-item.carousel-item {
  height: 100%;
}
.product.carousel.slide .banner.carousel-inner .banner-item.carousel-item img {
  object-fit: contain;
  position: relative;
}
.product.carousel.slide .banner-item.carousel-item.active .banner-info {
  position: absolute;
  top: 70%;
  left: 50%;
  transform: translate(-50%);
  width: 90%;
  z-index: 1;
  background-color: rgba(255, 255, 255, 0.712);
}
.banner-info p {
  color: rgb(0, 0, 0);
}
.banner-info button {
  color: rgb(31, 4, 4);
  /* border: 2px solid white; */
}
button.addtocart {
  background-color: rgb(189, 50, 40);
  color: white;
  border: none;
}
button.addtocart:hover {
  color: white;
}
